body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  height: auto;
}


#logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 70px;
  pointer-events: none;
}

.topTitle {
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  background-color: #c5a901;
}

.allButtons {
    box-sizing: border-box;
    color: #5c4925;
    letter-spacing: normal;
    text-transform: none;
    border-radius: 6px;
    background: #fff;
    font-size: 16px;
    border-color: #5c4925;
    border-width: 2px;
    border-style: solid;
    padding: .6em 1em;
}

.links {
  text-decoration: none;
  color: inherit
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#brewMain {
  background-color: #d1d0bb;
  height: 100%;
}

#startBatch {
  margin-top: 32px;
  margin-bottom: 32px;
  text-align: center;
}

.batchValue {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

#batchEnter {
  margin-top: 8px;
  text-align: center
}

h2 {
  margin: 0;
  padding-top: 16px;
  text-align: center;
  font-family: 'Rakkas', cursive;
  font-weight: normal;
  font-size: 30px;
  color:  black;
}

.userInputs {
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 32px;
  padding-bottom: 32px;
  border: 2px solid #282c34;
  border-radius: 6px;
  background-color: white;
}

.cFermenter {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
  border: 2px solid #5c4925;
  width: 250px;
  height: 250px;
}

.TankNumber {
  font-size: 20px;
  color: #5c4925;
  background-color: white;
  margin-left: 30%;
  margin-right: 30%;
  margin-bottom: 8px;
  padding-bottom: 6px;
  border: 2px solid white;
  border-radius: 6px;
}

.fermenterData, h4 {
  margin: 0;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

